/* Remove default margins and set background */
html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background: black;
  font-family: Arial, sans-serif;
}

/* Make the container take full viewport */
#root {
  position: relative;
}

/* Full-screen canvas */
canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
}

/* Neon green overlay text */
.status-overlay {
  position: absolute;
  bottom: 1.5rem;
  left: 50%;
  transform: translateX(-50%);
  color: #39ff14; /* Neon green */
  font-size: 1.25rem;
  font-weight: bold;
  text-align: center;
  text-shadow: 0 0 5px #39ff14, 0 0 10px #39ff14;
  padding: 0.5rem 1rem;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 6px;
}

/* Hide video element */
video {
  display: none;
}
